import React from 'react';

import '../../styles/AboutPage.scss';

const AboutPage = () => {
    return (
        <div className='aboutme-section'>
            <section>
                <h1>Soy realizador audiovisual</h1>
                <p>Hola, soy JuanB. Mis destrezas en realizacin audiovisual estn soportadas en una experiencia de muchos aos.<br/><br/>
                    Para mi creacion audiovisual es fundamental la observacion, a traves de ella, voy indagando sobre las relaciones entre los objetos, las cosas, los procesos, las historias, las situaciones, la misma vida; con las personas, sus cuestionamientos, sus temores, sus alegrias, en otras palabras, la confrontacion vida y vida, vidas vividas, o muertes, tambien es una forma de vida en pasado.<br/><br/>
                    La busqueda es indispensable como herramienta para encontrar algo que me impulsa a expresar sensaciones, ideas y emociones.<br/><br/>
                    Las imagenes y sonidos expresados desde mi interior, son registrados en soporte analogo o digital, tanto imagen fija como en movimiento; es la captura de instantes con los que juego a traves del espacio y del tiempo, intentando encontrar una respuesta que me ayude a comprender este entramado de experiencias que al fin y al cabo fluyen en instantes eternamente.<br/><br/>
                    Si algo encuentra un observador que le haga detenerse a mirar mi trabajo, entonces, he logrado mi cometido porque mi expresion rebota continuamente como el eco en la cueva.
                </p>
            </section>
            <aside>
            <img src='https://juanb.pro/img/juanb.jpg' alt='realizador audiovisual y fotgrafo, en medio del campo con una montaa en el fondo, rodeado de equipos de grabacin, mirando hacia la distancia, inspirado por la naturaleza y capturando momentos nicos con su cmara.'/>
                
            </aside>
        </div>
    );
}

export default AboutPage;
